<template>
	<div class="wrapper">
		<ScoreBoard :playerScore="playerScore" :computerScore="computerScore" />
		<Game @game-winner="getWinner" />
	</div>
</template>

<script>
import ScoreBoard from "../components/ScoreBoard";
import Game from "../components/Game";
export default {
	name: "Home",
	components: {
		ScoreBoard,
		Game,
	},
	data() {
		return {
			playerScore: 0,
			computerScore: 0,
		};
	},
	methods: {
		getWinner(winner) {
			if (winner == "player") {
				this.playerScore++;
			} else if (winner == "computer") {
				this.computerScore++;
			}
		},
	},
};
</script>

