<template>
	<div>
		<div class="game">
			<div class="players">
				<div class="hand" :class="{ 'player-hand': animationOn }">
					<i :class="'far fa-hand-' + playerChoice"></i>
				</div>
			</div>
			<GameResult class="result" :result="result" />
			<div class="players">
				<div class="hand" :class="{ 'computer-hand': animationOn }">
					<i class="comp" :class="'far fa-hand-' + computerChoice"></i>
				</div>
			</div>
		</div>
		<GameChoice @player-choice="getPlayerChoice" :can-click="canClick" />
	</div>
</template>

<script>
import GameResult from "../components/GameResult";
import GameChoice from "../components/GameChoice";
export default {
	name: "Game",
	components: {
		GameResult,
		GameChoice,
	},
	data() {
		return {
			canClick: true,
			animationOn: false,
			playerChoice: "rock",
			computerChoice: "rock",
			winner: "",
		};
	},
	methods: {
		getPlayerChoice(choice) {
			this.animationOn = true;
			this.playerChoice = "rock";
			this.computerChoice = "rock";
			this.canClick = false;

			setTimeout(() => {
				this.playerChoice = choice;
				this.getComputerChoice();
				this.getWinner();
				this.animationOn = false;
				this.$emit("game-winner", this.winner);
				this.canClick = true;
			}, 4000);
		},
		getComputerChoice() {
			let num = Math.floor(Math.random() * 100);
			if (num <= 33) {
				this.computerChoice = "rock";
			} else if (num > 33 && num < 66) {
				this.computerChoice = "paper";
			} else {
				this.computerChoice = "scissors";
			}
		},
		getWinner() {
			if (this.playerChoice === this.computerChoice) {
				this.winner = "draw";
			} else {
				if (this.playerChoice == "rock") {
					if (this.computerChoice == "scissors") {
						this.winner = "player";
					} else if (this.computerChoice == "paper") {
						this.winner = "computer";
					}
				} else if (this.playerChoice == "paper") {
					if (this.computerChoice == "rock") {
						this.winner = "player";
					} else if (this.computerChoice == "scissors") {
						this.winner = "computer";
					}
				} else if (this.playerChoice == "scissors") {
					if (this.computerChoice == "paper") {
						this.winner = "player";
					} else if (this.computerChoice == "rock") {
						this.winner = "computer";
					}
				}
			}
		},
	},
	computed: {
		result() {
			if (this.winner == "computer") {
				return "Defeat";
			} else if (this.winner == "player") {
				return "Victory";
			} else {
				return "Draw";
			}
		},
	},
};
</script>

<style lang="scss" scoped>
.game {
	margin: 2.3rem;
	padding: 1rem;
	display: flex;
	justify-content: space-between;

	.players {
		margin: 3rem;

		.hand {
			i {
				font-size: 10rem;
				transform: rotate(90deg);
				-moz-transform: rotate(90deg);
				-webkit-transform: rotate(90deg);
				-ms-transform: rotate(90deg);

				&.comp {
					transform: scaleX(-1) rotate(90deg);
					-moz-transform: scaleX(-1) rotate(90deg);
					-webkit-transform: scaleX(-1) rotate(90deg);
					-ms-transform: scaleX(-1) rotate(90deg);
				}
			}
		}

		.player-hand {
			animation: tiltPlayer 2000ms infinite;
		}

		.computer-hand {
			animation: tiltComputer 2000ms infinite;
		}
	}
	.result {
		margin: auto;
	}
}

@keyframes tiltPlayer {
	33% {
		transform: rotate(20deg);
		transform-origin: bottom left;
	}
	100% {
		transform: rotate(-20deg);
		transform-origin: bottom left;
	}
}

@keyframes tiltComputer {
	33% {
		transform: rotate(20deg);
		transform-origin: bottom right;
	}
	100% {
		transform: rotate(-20deg);
		transform-origin: bottom right;
	}
}
@media screen and (max-width: 768px) {
	.game {
		margin: 1rem;

		.players {
			margin: 1.6rem;

			.hand {
				i {
					font-size: 5rem;
				}
			}
		}
	}
}

@media screen and (max-width: 426px) {
	.game {
		margin: 0;
		padding: 0;

		.players {
			margin: 0.4rem;
		}
	}
}
</style>