<template>
	<div v-if="canClick" class="choice">
		<p @click="this.$emit('player-choice', 'rock')">Rock</p>
		<p @click="this.$emit('player-choice', 'paper')">Paper</p>
		<p @click="this.$emit('player-choice', 'scissors')">Scissors</p>
	</div>
</template>

<script>
export default {
	name: "GameChoice",
	props: {
		canClick: Boolean,
	},
};
</script>

<style lang="scss" scoped>
.choice {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	margin: 2rem auto;
	text-align: center;

	p {
		font-size: 1.4rem;
		margin: 1rem;
		border-radius: 24px;

		&:hover {
			cursor: pointer;
			color: white;
			background: #7aecb9;
			transition: 500ms ease-in;
		}
	}
}
@media screen and (max-width: 768px) {
	.choice {
		margin: 1.5rem auto;

		p {
			font-size: 1.2rem;
		}
	}
}
</style>